import React, { useEffect, useState } from "react"
import { StyledSideBar, StyledNavItem, StyledNav } from "./styles"
import { Logo } from "../Logo"
import { useLocation } from "@reach/router"

export const SideBar = ({ layout }) => {
  const [offset, setOffset] = useState(0)
  useEffect(() => {
    setOffset(layout.current?.scrollTop ?? 0)
    layout.current.onscroll = function (e) {
      setOffset(layout.current?.scrollTop ?? 0)
    }
  }, [layout])
  return (
    <StyledSideBar show={offset > 0}>
      <Logo />
      <StyledNav>
        <NavItem title={"Hello"} to={"#hello"} />
        <NavItem title={"Services"} to={"#services"} />
        <NavItem title={"Shop"} to={"#shop"} />
        <NavItem title={"Blog"} to={"#blog"} />
        <NavItem title={"Contact"} to={"#contact"} />
      </StyledNav>
    </StyledSideBar>
  )
}

export const NavItem = ({ title, to }) => {
  const location = useLocation()
  useEffect(() => {
    console.log(location.hash)
  }, [])
  return (
    <StyledNavItem to={`/${to}`} active={to === location.hash||(to==="#hello"&&location.hash==="")}>
      <span>{"<"}</span>
      {title}
      <span>{">"}</span>
    </StyledNavItem>
  )
}
