import styled from "@emotion/styled"
import { Link } from "gatsby"
import {
  backgroundColor,
  backgroundDarkColor,
  themeColor,
} from "../../styles/colors"
import React from "react"

export const StyledSideBar = styled.header`
  display: grid;
  grid-template-rows: max-content 1fr;
  height: 100vh;
  width: max-content;
  padding: 20px 8px;
  align-items: center;
  justify-items: center;
  background-color: ${backgroundColor};
  border-right: 1px solid ${backgroundDarkColor};
  box-shadow: ${({ show }) =>
    show ? "0 0 1px rgba(0, 0, 0, 0.6), 0 0 1px rgba(0, 0, 0, 0.12)" : ""};
`

export const StyledNav = styled.nav`
  display: flex;
  flex-direction: column;
  align-items: center;
`
export const StyledNavItem = styled(({ active, ...props }) => (
  <Link {...props} />
))`
  display: flex;
  padding: 24px 8px;
  text-decoration: none;
  font-size: 12px;
  align-content: center;
  align-items: center;
  transition: all 0.3s;
  color: ${({ active }) => (active ? themeColor : "white")};
  span {
    transition: all 0.3s;
    color: ${({ active }) => (active ? themeColor : "transparent")};
  }
`
